@import url(https://fonts.googleapis.com/css?family=Libre+Baskerville|Mali|Raleway&display=swap);
body {
  margin: 0;
  padding: 0;
}

.App {
  height: 100vh;
  /* background-color: #abe1fc; */
  display: flex;
  background: linear-gradient(135deg, #abe1fc 0%, #abe1fc 50%, rgb(240, 98, 146) 50%, rgb(240, 98, 146) 100%);
  color: rgb(73, 73, 73);
}

.Joke {
  padding: 0.5em 1em;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  text-align: start;
}

.Joke-text {
  /* text-align: center; */
}

.Joke-left {
  min-width: 100px;
}

.Joke-arrow {
  font-size: 1.5em;
  color: grey;
  background: none;
  border: none;
  outline: none;
}

.Joke-arrow-up:hover {
  cursor: pointer;
  -webkit-animation: move-up 0.7s ease-out;
          animation: move-up 0.7s ease-out;
  color: #8bc34a;
}

.Joke-arrow-down:hover {
  cursor: pointer;
  -webkit-animation: move-down 0.7s ease-out;
          animation: move-down 0.7s ease-out;
  color: #f44336;
}

.Joke-rating {
  width: 2em;
  height: 2em;
  line-height: 2em;
  vertical-align: bottom;
  display: inline-block;
  border-radius: 50%;
  text-align: center;
  background-color: white;
  box-shadow: 4px 5px 19px -5px rgba(0, 0, 0, 0.45);
  font-weight: bold;
}

.Joke-emoji {
  margin-left: auto;
  margin-right: 0;
  justify-self: flex-end;
  -webkit-transition: -webkit-transform 0.8s ease-in-out;
  transition: -webkit-transform 0.8s ease-in-out;
  transition: transform 0.8s ease-in-out;
  transition: transform 0.8s ease-in-out, -webkit-transform 0.8s ease-in-out;
}

.Joke-emoji:hover {
  -webkit-transform: rotate(360deg);
          transform: rotate(360deg);
  cursor: default;
}

.Joke-emoji-roll {
  -webkit-animation: spin 1s infinite;
          animation: spin 1s infinite;
}

@-webkit-keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes spin {
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@-webkit-keyframes move-up {
  0%,
  50%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25%,
  75% {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
  }
}

@keyframes move-up {
  0%,
  50%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25%,
  75% {
    -webkit-transform: translateY(-3px);
            transform: translateY(-3px);
  }
}

@-webkit-keyframes move-down {
  0%,
  50%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25%,
  75% {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
  }
}

@keyframes move-down {
  0%,
  50%,
  100% {
    -webkit-transform: translateY(0);
            transform: translateY(0);
  }
  25%,
  75% {
    -webkit-transform: translateY(3px);
            transform: translateY(3px);
  }
}

.JokeList {
  width: 800px;
  height: 550px;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  background-color: white;
  overflow-y: auto;
  margin: 2em 0;
  font-size: 0.9em;
  font-family: 'Raleway', sans-serif;
  box-shadow: 0 8px 6px -6px black;
}

@media (max-width: 992px) {
  .JokeList {
    width: 100%;
    height: auto;
    margin-top: 0;
  }
}

.Header {
  background-color: #8b69c6;
  width: 260px;
  height: 600px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-family: 'Mali', cursive;
  box-shadow: 0 8px 6px -6px black;
}

.Header-button {
  margin: 0.67em 0;
  outline: none;
  border: none;
  background: linear-gradient(135deg, #abe1fc 0%, #abe1fc 50%, rgb(240, 98, 146) 50%, rgb(240, 98, 146) 100%);
  padding: 1em;
  color: white;
  font-size: 1.2em;
  font-weight: bold;
  text-transform: uppercase;
  border-radius: 1em;
  font-family: inherit;
  box-shadow: 0 8px 6px -6px black;
  -webkit-transition: all 0.2s;
  transition: all 0.2s;
}

.Header-title {
  color: white;
  font-size: 3rem;
  margin: 0;
}

.Header-title-top {
  font-family: 'Libre Baskerville';
  text-transform: uppercase;
  display: block;
}

.Header-button:hover {
  cursor: pointer;
  -webkit-transform: translateY(-0.1em);
          transform: translateY(-0.1em);
}

.Header-button[disabled] {
  background: none;
  background-color: grey;
  cursor: not-allowed;
  color: white;
}

.Header-icon {
  box-shadow: 0 -8px 6px -6px black;
  border-radius: 50%;
  padding: 1.5rem;
  margin: 1.5rem;
}

.Header-icon:hover {
  cursor: default;
  -webkit-animation: wobble 0.6s ease infinite;
          animation: wobble 0.6s ease infinite;
}

@media (max-width: 992px) {
  .Header {
    flex-direction: row;
    justify-content: space-evenly;
    width: 100%;
    height: auto;
  }

  .Header-icon {
    height: 6em;
    margin: 0;
  }
}

@media (max-width: 768px) {
  .Header-title {
    font-size: 1.5rem;
  }
  .Header-button {
    padding: 0.3rem;
    font-size: 0.8rem;
  }
  .Header-icon {
    padding: 0.8rem;
  }
}

@-webkit-keyframes wobble {
  0% {
    -webkit-transform: translate(3px, 2px) rotate(0deg);
  }
  10% {
    -webkit-transform: translate(-2px, -3px) rotate(-2deg);
  }
  20% {
    -webkit-transform: translate(-4px, 0px) rotate(2deg);
  }
  30% {
    -webkit-transform: translate(0px, 3px) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(2px, -2px) rotate(2deg);
  }
  50% {
    -webkit-transform: translate(-2px, 3px) rotate(-2deg);
  }
  60% {
    -webkit-transform: translate(-4px, 2px) rotate(0deg);
  }
  70% {
    -webkit-transform: translate(3px, 2px) rotate(-2deg);
  }
  80% {
    -webkit-transform: translate(-2px, -2px) rotate(2deg);
  }
  90% {
    -webkit-transform: translate(3px, 3px) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(2px, -3px) rotate(-2deg);
  }
}

@keyframes wobble {
  0% {
    -webkit-transform: translate(3px, 2px) rotate(0deg);
  }
  10% {
    -webkit-transform: translate(-2px, -3px) rotate(-2deg);
  }
  20% {
    -webkit-transform: translate(-4px, 0px) rotate(2deg);
  }
  30% {
    -webkit-transform: translate(0px, 3px) rotate(0deg);
  }
  40% {
    -webkit-transform: translate(2px, -2px) rotate(2deg);
  }
  50% {
    -webkit-transform: translate(-2px, 3px) rotate(-2deg);
  }
  60% {
    -webkit-transform: translate(-4px, 2px) rotate(0deg);
  }
  70% {
    -webkit-transform: translate(3px, 2px) rotate(-2deg);
  }
  80% {
    -webkit-transform: translate(-2px, -2px) rotate(2deg);
  }
  90% {
    -webkit-transform: translate(3px, 3px) rotate(0deg);
  }
  100% {
    -webkit-transform: translate(2px, -3px) rotate(-2deg);
  }
}

.DadJokes {
  max-width: 1000px;
  max-height: 600px;
  margin: auto;
  display: flex;
  overflow: visible;
}

@media (max-width: 992px) {
  .DadJokes {
    flex-direction: column;
    width: 95vw;
    height: 95vh;
    margin: auto;
  }
}

